import React from 'react';
import Types from 'prop-types';
import classNames from 'classnames';

export const BUTTON_VARIANTS = ['primary', 'pay', 'secondary', 'danger'];
export const BUTTON_SIZES = ['xs', 'sm', 'md', 'lg'];
// export const BUTTON_LAYOUTS = ['default', 'dropdown', 'split']; TODO: add different buttons in separate ticket
export const BUTTON_STATES = ['default', 'loading', 'disabled'];

const Button = ({ label, buttonState, size, variant, isBlock, onClick }) => {
  const isDisabled = buttonState === 'disabled';
  const isLoading = buttonState === 'loading';

  const classes = classNames(`btn btn-${size}`, {
    'btn-loading': isLoading,
    'btn-primary': variant === 'primary',
    'btn-success': variant === 'pay',
    'btn-default': variant === 'default',
    'btn-danger': variant === 'danger',
    'btn-block': isBlock,
  });

  return (
    <button
      type="button"
      className={classes}
      onClick={e => onClick(e)}
      disabled={isDisabled || buttonState === 'loading'}
    >
      {label}
      {isLoading && <span className={`btn-loader ${isBlock ? 'pull-left' : 'm-l-2'}`} />}
    </button>
  );
};

Button.propTypes = {
  variant: Types.oneOf(BUTTON_VARIANTS),
  size: Types.oneOf(BUTTON_SIZES),
  buttonState: Types.oneOf(BUTTON_STATES),
  isBlock: Types.bool,
  onClick: Types.func.isRequired,
  label: Types.string.isRequired,
};

Button.defaultProps = {
  size: 'md',
  variant: 'primary',
  buttonState: 'default',
  isBlock: false,
};

export default Button;



